<template>
  <div>
    <el-row type="flex"
            class="row-bg"
            :gutter="30"
            justify="space-around">
      <el-col :span="6"
              class="columnflex">
        <el-card class="cards">
          <h1>个人信息</h1>
          <el-button type="primary"
                     icon="el-icon-search"
                     @click="go1">点击查看详细信息</el-button>
        </el-card>
        <el-card class="cards">
          <h2>学生预警</h2>
          <el-tag effect="dark"
                  type="success">
            良好（暂无预警）
          </el-tag>
        </el-card>
      </el-col>
      <el-col :span="6">
        <el-card class="student">
          <el-image class="avatar"
                    :src="require('@/assets/mstudent.png')">
          </el-image>
          <div class="base">
            <div class="name"> {{name}}</div>

            <div> {{term}}</div>
          </div>

        </el-card>
      </el-col>
      <el-col :span="6"
              class="columnflex">
        <el-card class="cards">
          <h1>课程信息</h1>
          <el-button type="primary"
                     icon="el-icon-search"
                     @click="go2">点击查看详细信息</el-button>
        </el-card>
        <el-card class="cards">
          <h2>学生评价</h2>
          <el-rate v-model="value"
                   disabled
                   show-score
                   text-color="#ff9900"
                   score-template="{value}"
                   class="rate">
          </el-rate>
        </el-card>
      </el-col>
    </el-row>

  </div>
</template>

<script>
export default {
  name: 'baseInfo',
  data () {
    return {
      value: 4.6,
      name: '',
      sex: '',
      term: '',
      course: []
    }
  },
  mounted () {
    var id = window.sessionStorage.getItem('storeData')
    this.getInfo(id)
    this.getStu(id)
  },
  methods: {
    go1 () {
      this.$router.push('/index/perInfo')
    },
    go2 () {
      this.$router.push('/index/studyInfo')

    },
    getInfo (id) {
      this.$ajax({
        url: 'http://119.29.243.108:8080/tbaps/stu/perInfo',
        method: 'get',
        params: {
          stuId: id,
        }
      }).then(res => {
        console.log(res);
        this.name = res.data.data.name
        this.sex = res.data.data.sex
        this.term = res.data.data.term
      }).catch(error => {
        this.$message.error('错误')
        console.log(error);
      })
    },
    getStu (id) {
      this.$ajax({
        url: 'http://119.29.243.108:8080/tbaps/learnInfo/courseList',
        method: 'get',
        params: {
          stuId: id,
        }
      }).then(res => {
        console.log(res);
        this.course = res.data.data
      }).catch(error => {
        this.$message.error('错误')
        console.log(error);
      })
    }
  },
  components: {

  }
}
</script>

<style>
.columnflex {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.cards {
  text-align: center;
  /* border: rgb(203, 203, 203) 1px solid; */
  min-height: 160px;
  margin: 20px 0;
}
.student {
  background-color: #f5f2f2;
}
.el-rate {
  line-height: 2;
}
.base {
  text-align: center;
}
.name {
  font-size: 30px;
}
</style>





